import { Component, OnInit } from '@angular/core';
import { Favs, Film, FilmDetail, SerieDetail } from '../interfaces/interfaces';
import { JustwatchService } from '../services/justwatch.service';
import { AuthService } from '../auth/auth.service';
import { TmdbService } from '../services/tmdb.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-tab3',
  templateUrl: 'tab3.page.html',
  styleUrls: ['tab3.page.scss']
})
export class Tab3Page implements OnInit  {

  auth: boolean;
  favs: Favs;
  film: FilmDetail;
  serie: SerieDetail;
  result: Film = {
      adult: false,
      backdrop_path: "",
      genre_ids: [],
      id: 0,
      original_language: "",
      original_title: "",
      original_name: "",
      first_air_date: "",
      overview: "",
      popularity: 0,
      poster_path: "",
      release_date: "",
      title: "",
      name: "",
      video: false,
      vote_average: 0,
      vote_count: 0
  };
  results: Film[] = [];

  constructor(private justWatch: JustwatchService,
    public authService: AuthService,
    private tmdb: TmdbService,
    private router: Router) {
  }

  ngOnInit(): void {
    this.loadFavs();
    this.isAuth();
  }

  loadFavs() {
    this.justWatch.getFavorites().subscribe(res => {
      this.favs = res
      this.favs.favorite.forEach(id => {
        this.tmdb.getMovieDetail(String(id)).subscribe(
          res=>{
            this.result.id = res.id;
            this.result.poster_path = res.poster_path;
            this.result.original_title = res.original_title;
            this.result.release_date = res.release_date
            this.results.push(this.result)
            this.result = {
                adult: false,
                backdrop_path: "",
                genre_ids: [],
                id: 0,
                original_language: "",
                original_title: "",
                original_name: "",
                first_air_date: "",
                overview: "",
                popularity: 0,
                poster_path: "",
                release_date: "",
                title: "",
                name: "",
                video: false,
                vote_average: 0,
                vote_count: 0
            }
          },
          err => {
            this.tmdb.getSerieDetail(String(id)).subscribe(
              res=>{
                this.result.id = res.id;
                this.result.poster_path = res.poster_path;
                this.result.original_name = res.original_name;
                this.result.first_air_date= res.first_air_date;
                this.results.push(this.result)
                this.result = {
                    adult: false,
                    backdrop_path: "",
                    genre_ids: [],
                    id: 0,
                    original_language: "",
                    original_title: "",
                    original_name: "",
                    first_air_date: "",
                    overview: "",
                    popularity: 0,
                    poster_path: "",
                    release_date: "",
                    title: "",
                    name: "",
                    video: false,
                    vote_average: 0,
                    vote_count: 0
                }
              }
            )
          }
        )
      });
    })
  }

  isAuth() {
    this.authService.whoAmI().subscribe(res => {
      this.auth = true;
    }, err => {
      this.auth = false;
    })
  }

  async showDetails(id: string, title: string, name: string): Promise<void>{
    let style = '';
    let _name = ''
    if(name != undefined){
      style = 'tv'
      _name = name
    }
    if(title != undefined){
      style = 'movie'
      _name = title
    }
    this.router.navigate([`/app/details/${id}/${style}/${_name}`])
  }

}
